<template>
  <div class="hud">
    <health class="hud__health"/>

    <timer class="hud__timer"/>

    <score class="hud__score"/>

    <cross-hair class="hud__crosshair"/>

    <v-alert class="hud__alert" :duration="5000">
      <p>Move your phone to look around.</p>
      <p>Tap anywhere to shoot.</p>
    </v-alert>

    <v-modal class="hud__paused" v-if="!isPlaying">
      <span class="hud__paused__icon">
        <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0z" fill="none"/><path d="M6 19h4V5H6v14zm8-14v14h4V5h-4z"/></svg>
      </span>

      <div class="hud__paused__title">Game Paused!</div>

      <div class="hud__paused__actions">
        <v-button to="/">Leave Game</v-button>
        <v-button @click="play">Continue</v-button>
      </div>
    </v-modal>

    <v-button class="hud__reload" type="fab" @click="reload">
      <svg xmlns="http://www.w3.org/2000/svg" height="36" viewBox="0 0 24 24" width="36"><path d="M0 0h24v24H0z" fill="none"/><path d="M12 6v3l4-4-4-4v3c-4.42 0-8 3.58-8 8 0 1.57.46 3.03 1.24 4.26L6.7 14.8c-.45-.83-.7-1.79-.7-2.8 0-3.31 2.69-6 6-6zm6.76 1.74L17.3 9.2c.44.84.7 1.79.7 2.8 0 3.31-2.69 6-6 6v-3l-4 4 4 4v-3c4.42 0 8-3.58 8-8 0-1.57-.46-3.03-1.24-4.26z"/></svg>
    </v-button>

    <toggle-fullscreen class="hud__fullscreen"/>
  </div>
</template>

<script>
import AFRAME from 'aframe';
import { mapGetters, mapActions } from 'vuex';

import VModal from '@/components/common/VModal.vue';
import VButton from '@/components/common/VButton.vue';
import VAlert from '@/components/common/VAlert.vue';
import Health from './components/Health.vue';
import Timer from './components/Timer.vue';
import Score from './components/Score.vue';
import CrossHair from './components/CrossHair.vue';
import ToggleFullscreen from './components/ToggleFullscreen.vue';

export default {
  name: 'Hud',
  components: {
    VModal,
    VButton,
    VAlert,
    Health,
    Timer,
    Score,
    CrossHair,
    ToggleFullscreen,
  },
  computed: {
    ...mapGetters('game', [
      'isPlaying',
    ]),
  },
  methods: {
    ...mapActions('game', [
      'play',
    ]),
    reload() {
      AFRAME.scenes[0].emit('reload');
    },
  },
};
</script>

