<template>
  <div class="alert" ref="alert">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'Alert',
  props: {
    duration: {
      type: Number,
      default: 3000,
    },
  },
  methods: {
    show() {
      const { alert } = this.$refs;

      alert.classList.add('alert--active');
    },
    hide() {
      const { alert } = this.$refs;

      alert.classList.remove('alert--active');
    },
  },
  mounted() {
    this.show();

    setTimeout(() => {
      this.hide();
    }, this.duration);
  },
};
</script>

<style lang="scss" scoped>
.alert {
  color: #a4a4a4;
  opacity: 0;
  transition: opacity .5s;
  font-size: 1rem;

  &--active {
    opacity: 1;
  }
}
</style>
