<template>
  <div class="fullscreen-button" @click="toggleFullScreen">
    <v-button v-if="isFullScreen" type="fab">
      <svg xmlns="http://www.w3.org/2000/svg" height="36" viewBox="0 0 24 24" width="36"><path d="M0 0h24v24H0z" fill="none"/><path d="M5 16h3v3h2v-5H5v2zm3-8H5v2h5V5H8v3zm6 11h2v-3h3v-2h-5v5zm2-11V5h-2v5h5V8h-3z" fill="#a4a4a4"/></svg>
    </v-button>
    <v-button v-else type="fab">
      <svg xmlns="http://www.w3.org/2000/svg" height="36" viewBox="0 0 24 24" width="36"><path d="M0 0h24v24H0z" fill="none"/><path d="M7 14H5v5h5v-2H7v-3zm-2-4h2V7h3V5H5v5zm12 7h-3v2h5v-5h-2v3zM14 5v2h3v3h2V5h-5z" fill="#a4a4a4"/></svg>
    </v-button>
  </div>
</template>

<script>
import VButton from '@/components/common/VButton.vue';

export default {
  name: 'ToggleFullscreen',
  components: {
    VButton,
  },
  data() {
    return {
      isFullScreen: false,
    };
  },
  methods: {
    toggleFullScreen() {
      if (!document.fullscreenElement) {
        document.documentElement.requestFullscreen();
        this.isFullScreen = true;
      } else {
        document.exitFullscreen();
        this.isFullScreen = false;
      }
    },
  },
};
</script>
