<template>
  <button :class="['button', `button--${type}`]" @click="click">
    <slot></slot>
  </button>
</template>

<script>
const buttonTypes = {
  PRIMARY: 'primary',
  FAB: 'fab',
};

export default {
  name: 'VButton',
  props: {
    type: {
      type: String,
      default: buttonTypes.PRIMARY,
    },
    to: {
      type: String,
      required: false,
    },
  },
  methods: {
    click(event) {
      if (this.to) {
        this.$router.push({ path: this.to });
      }

      this.$emit('click', event);
    },
  },
};
</script>

<style lang="scss" scoped>
