<template>
  <div class="game">
    <a-scene
      vr-mode-ui="enabled: false"
    >
      <a-assets ref="assets">
        <a-asset-item id="virus1" src="static_assets/models/Virus01.glb"></a-asset-item>
      </a-assets>

      <!-- Bullet Template -->
      <a-entity
        bullet="name: normal; speed: 30.0; poolSize: 20"
        geometry="primitive: icosahedron; radius: 0.1;"
        material="color: #000; emissiveIntensity: 0.5; emissive: #000">
      </a-entity>

      <!-- Enemies -->
      <a-entity
        id="enemy1"
        class="enemies"
        animation-mixer
        target="static: false"
        enemy="assetId: virus1;"
        random-spawn="min: -10 -1 -10;
                      max: 10 2 10;
                      radius: 2"
        visible="false"
        animation="property: position;
                   to: 0 1.6 0;
                   dur: 20000;
                   easing: easeInOutCubic;
                   startEvents: spawn;
                   pauseEvents: die"
      ></a-entity>

      <!-- Player -->
      <a-entity
        id="player"
        position="0 1.6 0"
        sphere-collider="objects: .enemies; radius: 0.5"
        geometry="primitive: sphere; radius: 0.5"
      >
        <a-camera
          id="camera"
          position="0 0 0"
          shooter="activeBulletType: normal"
          player="ammo: 10"
        >
        </a-camera>
      </a-entity>
    </a-scene>

    <component :is="hudComponent" @reload="reload"></component>
  </div>
</template>

<script>
import 'aframe';
import 'aframe-super-shooter-kit';
import 'aframe-extras';
import 'aframe-proxy-event-component';
import 'aframe-look-at-component';

// Aframe Components
import './components/player';
import './components/enemy';
import './components/randomSpawn';

// Vue Components
import Hud from '@/components/Hud/Hud.vue';

// Utils
import camera from './utils/camera';

export default {
  name: 'Game',
  components: {
    Hud,
  },
  data() {
    return {
      hudComponent: undefined,
    };
  },
  mounted() {
    const { assets } = this.$refs;

    assets.addEventListener('loaded', () => {
      this.hudComponent = Hud;
      camera.init();
    });
  },
};
</script>
