/* eslint-disable no-shadow */

const state = {
  score: 0,
  health: 100,
};

const getters = {
  score: ({ score }) => score,
  health: ({ health }) => health,
};

const mutations = {
  incrementScore(state, score) {
    state.score += score;
  },
  decrementHealth(state, damage) {
    state.health -= damage;
  },
};

const actions = {
  incrementScore({ commit }, score) {
    commit('incrementScore', score);
  },
  decrementHealth({ commit, dispatch, state }, damage) {
    if (state.health - damage <= 0) {
      dispatch('game/finishGame', null, { root: true });
    } else {
      commit('decrementHealth', damage);
    }
  },
};

export default {
  namespaced: true,
  actions,
  getters,
  mutations,
  state,
};
