/**
 * Enemy Spawn
 *
 * Implement random spawning of an entity.
 *
 * @property {vec3} min - Minimum Vector3 position default '0 0 0'.
 * @property {vec3} max - Maximum Vector3 position default '0 0 0'.
 * @property {vec3} playerPos - Position of the player collision sphere.
 * @property {number} radius - Exclude positions within a radius from the center '0 0 0'.
 * Best used to limit spawn positions from being too close from the player collision sphere.
 *
 */

import AFRAME from 'aframe';

AFRAME.registerComponent('random-spawn', {
  schema: {
    min: { type: 'vec3', default: { x: 0, y: 0, z: 0 } },
    max: { type: 'vec3', default: { x: 0, y: 0, z: 0 } },
    playerPos: { type: 'vec3', default: { x: 0, y: 1.6, z: 0 } },
    radius: { type: 'number', default: 1 },
  },
  init() {
    const { el } = this;

    this.spawn = this.spawn.bind(this);
    el.addEventListener('spawn', this.spawn);
  },
  randomNumber(min, max) {
    return Math.random() * (max - min) + min;
  },
  pointIsInSphere(point, sphereCenter, radius) {
    return Math.sqrt(
      ((point.x - sphereCenter.x) ** 2)
      + ((point.y - sphereCenter.y) ** 2)
      + ((point.z - sphereCenter.z) ** 2),
    ) <= radius;
  },
  spawn() {
    const { el, data } = this;
    const x = this.randomNumber(data.min.x, data.max.x);
    const y = this.randomNumber(data.min.y, data.max.y);
    const z = this.randomNumber(data.min.z, data.max.z);
    const isInRadius = this.pointIsInSphere({ x, y, z }, data.playerPos, data.radius);

    if (isInRadius) {
      this.spawn();
    } else {
      el.setAttribute('position', `${x} ${y} ${z}`);
      el.setAttribute('visible', 'true');
    }
  },
  remove() {
    this.el.removeEventListener('spawn', this.spawn);
  },
});
