<template>
  <div class="timer">
    <span class="timer__text">{{parsedTime}}</span>
    <button class="timer__pause" @click="pause">
      <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0z" fill="none"/><path d="M6 19h4V5H6v14zm8-14v14h4V5h-4z"/></svg>
    </button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'Timer',
  data() {
    return {
      time: 0,
    };
  },
  computed: {
    ...mapGetters('game', {
      storeTime: 'time',
      isPlaying: 'isPlaying',
    }),
    parsedTime() {
      const minutes = Math.floor(this.time / 60);
      const seconds = this.time - minutes * 60;

      return `${this.pad(minutes)}:${this.pad(seconds)}`;
    },
  },
  methods: {
    ...mapActions('game', [
      'setTime',
      'pause',
      'finishGame',
    ]),
    startTimer() {
      this.interval = setInterval(() => {
        if (this.isPlaying) {
          if (this.time > 0) {
            this.time -= 1;
          } else {
            this.setTime(this.time);
            this.finishGame();
            clearInterval(this.interval);
          }
        }
      }, 1000);
    },
    pad(number) {
      return number.toString().padStart(2, '0');
    },
  },
  mounted() {
    this.time = this.storeTime;
    this.startTimer();
  },
  removed() {
    clearInterval(this.interval);
  },
};
</script>

