/* eslint-disable no-shadow */
import AFRAME from 'aframe';

const state = {
  isPlaying: true,
  time: 120,
};

const getters = {
  isPlaying: ({ isPlaying }) => isPlaying,
  time: ({ time }) => time,
};

const mutations = {
  setIsPlaying(state, isPlaying) {
    state.isPlaying = isPlaying;
  },
  setTime(state, time) {
    state.time = time;
  },
};

const actions = {
  play({ commit }) {
    commit('setIsPlaying', true);

    AFRAME.scenes[0].play();
  },
  pause({ commit }) {
    commit('setIsPlaying', false);

    AFRAME.scenes[0].pause();
  },
  setTime({ commit }, time) {
    commit('setTime', time);
  },
  finishGame() {
    console.log('Game ended');
  },
};

export default {
  namespaced: true,
  actions,
  getters,
  mutations,
  state,
};
